div.dataTables_wrapper {
    div.dataTables_length {
        display: inline-block;
        margin-left: 16px;

        label {
            select {
                width: 70px;
            }
        }

    }

    div.dt-buttons {
        .btn-group {
            >button {
                padding: .25rem .5rem;
                font-size: .875rem;
                line-height: 1.5;
                border-radius: 0.1875rem;

                &.buttons-colvis {
                    padding-left: 1.25rem;
                    padding-right: 1.25rem;
                }
            }
        }

        .dropdown-menu {
            padding-top: 0;
        }
    }

    table.dataTable {
        tbody>tr {
            &.selected {
                background-color: #EEFFEE;
                color: #000;
            }

            // span.badge {
            //     margin-bottom: 4px;
            // }

            td {
                vertical-align: middle;
            }
        }

        tr {

            >th:first-child,
            >td:first-child {
                text-align: center;
                outline: none;
            }

            >th:first-child>.custom-control {
                padding-left: 2rem;

            }
        }
    }

    span.badge {
        border-radius: 0;
        padding: 0.5em 0.8em;
    }    
}


.row-selector {
    >input {
        position: relative;
        z-index: -9999;
    }

    padding-left: 1rem;
}

/*table header tool*/
.table-tool {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 10px;
    border-left: 5px solid #8bb4e7;
    background-color: #f5f8fd;
    padding-top: 10px;
    padding-bottom: 10px;

    >div:not(:last-child) {
        margin-bottom: 10px;
    }

    .table-select {
        display: inline-block;
        height: calc(2.25rem + 2px);
        padding: .375rem 1.75rem .375rem .75rem;
        font-weight: 400;
        line-height: 1.5;
        color: #495057;
        vertical-align: middle;
        background: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3e%3cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e") no-repeat right 0.75rem center/8px 10px;
        background-color: #fff;
        border: 1px solid #ced4da;
        border-radius: .25rem;
        -webkit-appearance: none;
        //sm
        height: calc(1.5em + 0.5rem + 2px);
        padding-top: 0.25rem;
        padding-bottom: 0.25rem;
        padding-left: 0.8rem;
        padding-right: 2rem;
        font-size: 0.875rem;
    }

}

//重写bootstrap-select的样式
.filter-container {
    .bootstrap-select {
        display: inline-block;
        max-width: 300px;
        background-color: transparent;
        font-size: 12px;
        line-height: 0;

        >button {
            background-color: #fff;
            border: 1px solid #ced4da;
            font-size: 14px;
            height: 31px;
            line-height: 31px;
            padding: 0 .75rem;
        }

        .dropdown-menu {
            margin-top: 2px;
        }
    }

    .bootstrap-select>.dropdown-toggle.bs-placeholder,
    .bootstrap-select>.dropdown-toggle.bs-placeholder:active,
    .bootstrap-select>.dropdown-toggle.bs-placeholder:focus,
    .bootstrap-select>.dropdown-toggle.bs-placeholder:hover {
        color: #495057;
    }
}