span.status {
  cursor: pointer;
}

.row-content {
  max-width: 500px;
  text-emphasis: emphasis;
  overflow-wrap: break-word;
  white-space: nowrap;
  overflow: hidden;
}
.row-content p:last-child {
  margin-bottom: 0;
}

.input-group-append .input-group-text,
.input-group-prepend .input-group-text {
  padding: 0.35rem 0.75rem;
  border: 1px solid #ced4da;
}